<template>
  <div class="app">
    <app-header />

    <div class="app-content">
      <base-modal v-if="baseModal" />

      <transition name="travel-form-modal">
        <travel-form v-if="travelFormModal" />
      </transition>

      <loader v-if="isLoading" />
      <router-view v-else />
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import firebase from '@/firebase'
import AppHeader from '@/components/AppHeader'
import BaseModal from '@/components/modals/BaseModal'
import Loader from '@/components/Loader'
import TravelForm from '@/components/modals/TravelForm'

export default {
  name: 'App',
  components: {
    AppHeader,
    BaseModal,
    Loader,
    TravelForm
  },
  beforeMount() {
    firebase.auth().onAuthStateChanged(user => {
      this.isLoading = true;

      if (user) {
        this.UPDATE_USER({
          email: user.email,
          uid: user.uid
        });

        this.GET_TRAVELS().then(() => {
          this.isLoading = false;
        });
      } else {
        this.isLoading = false;
      }
    });
  },
  data () {
    return {
      isLoading: true
    };
  },
  computed: {
    ...mapState(['baseModal', 'travelFormModal'])
  },
  methods: {
    ...mapActions(['GET_TRAVELS']),
    ...mapMutations(['UPDATE_USER']),
  }
}
</script>

