<template>
  <div class="user">
    <h2 v-show="userName" class="user__greeting">
      Welcome, {{ userName }}!
    </h2>

    <div class="user__navigation">
      <router-link
        class="user__link"
        title="Go to Home page"
        to="/"
        @click="handleLinkClick"
      >
        Home
      </router-link>

      <router-link
        class="user__link"
        title="Go to About page"
        to="/about"
        @click="handleLinkClick"
      >
        About
      </router-link>
    </div>

    <button
      class="user__logout"
      type="button"
      @click="logout"
    >
      Logout
    </button>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import firebase from '@/firebase'

export default {
  name: 'User',
  computed: {
    ...mapState(['me', 'mobileMenu']),
    userName () {
      const email = this.me?.email

      if (typeof email === 'string') {
        return email.split('@')[0];
      }

      return null;
    }
  },
  methods: {
    ...mapActions(['TERMINATE_SESSION']),
    ...mapMutations(['TOGGLE_BASE_MODAL', 'TOGGLE_MOBILE_MENU']),
    /**
     * Close the mobile menu if it's active
     */
    handleLinkClick () {
      if (this.mobileMenu) this.TOGGLE_MOBILE_MENU();
    },
    /**
     * Terminate user session
     */
    logout () {
      if (this.me) {
        firebase
          .auth()
          .signOut()
          .then(() => {
            this.TERMINATE_SESSION();
            this.$router.replace('/login')
          })
          .catch(err => {
            this.TOGGLE_BASE_MODAL({
              buttonResolve: 'Ok',
              text: err.message,
              title: 'Error'
            });
          });
      }
    }
  }
}
</script>

