<template>
  <router-link
    class="travel"
    :to="{
      name: 'Travel',
      params: { travelId: travel.id }
    }"
  >
    <div class="travel__data travel__data--id">
      <span>#{{ id }}</span>
    </div>

    <div class="travel__data travel__data--name">
      <span>{{ name }}</span>
    </div>

    <div class="travel__data travel__data--dest">
      <strong>{{ country }}</strong>
    </div>

    <div class="travel__data travel__data--time-left">
      <span class="travel__days-left">{{ daysLeft }}</span>
      {{ inflect('day', 'days', daysLeft) }}
      to travel
    </div>

    <div class="travel__data travel__data--status">
      <status :status="status" />
    </div>

    <div class="travel__data travel__data--icon">
      <img
        alt="Go"
        class="travel__icon"
        src="@/assets/icons/icon-caret-right.svg"
      />
    </div>
  </router-link>
</template>

<script>
import Inflector from '@/components/mixins/Inflector'
import MappingCountries from '@/components/mixins/MappingCountries'
import Status from '@/components/Status'

export default {
  name: 'Travel',
  components: {
    Status
  },
  mixins: [Inflector, MappingCountries],
  props: {
    travel: {
      required: true,
      type: Object
    }
  },
  data () {
    return { ...this.travel }
  },
  computed: {
    country () {
      return this.countries[this.destination]
    }
  }
}
</script>

