<template>
  <div class="mobile-menu">
    <button
      class="mobile-menu__trigger"
      type="button"
      @click="toggleMenu"
    >
      Hamburger Menu
    </button>

    <div
      class="mobile-menu__content"
      ref="content"
      :class="{ 'mobile-menu__content--hide': !mobileMenu }"
    >
      <slot></slot>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'

export default {
  name: 'MobileMenu',
  computed: {
    ...mapState(['mobileMenu'])
  },
  methods: {
    ...mapMutations(['TOGGLE_MOBILE_MENU']),
    toggleMenu () {
      this.TOGGLE_MOBILE_MENU();
    }
  }
}
</script>

