<template>
  <div class="modal">
    <div class="modal__content">
      <h2 class="modal__title">
        {{ baseModal.title }}
      </h2>

      <p class="modal__text">
        {{ baseModal.text }}
      </p>

      <div class="modal__actions">
        <button
          v-if="baseModal.buttonReject"
          class="modal__button modal__button--reject"
          type="button"
          @click="handleReject"
        >
          {{ baseModal.buttonReject }}
        </button>

        <button
          class="modal__button modal__button--accept"
          type="button"
          @click="handleResolve"
        >
          {{ baseModal.buttonResolve || 'Ok' }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'

export default {
  name: 'Modal',
  computed: {
    ...mapState(['baseModal'])
  },
  methods: {
    ...mapMutations(['TOGGLE_BASE_MODAL']),
    handleReject() {
      this.TOGGLE_BASE_MODAL(null);
    },
    handleResolve() {
      const { onResolve } = this.baseModal;

      if (typeof onResolve === 'function') onResolve();
      this.handleReject();
    }
  }
}
</script>

