import { createStore } from 'vuex'
import { firestore } from '@/firebase'
import getDateAttributes from '@/helpers/getDateAttributes'

export default createStore({
  state: {
    baseModal: null,
    isAuthenticated: false,
    me: null,
    mobileMenu: false,
    travelCurrent: null,
    travelFormModal: false,
    travelsData: []
  },
  mutations: {
    ADD_TRAVEL_DATA(state, payload) {
      state.travelsData.push(payload);
    },
    CLEAR_TRAVELS_DATA(state) {
      state.travelCurrent = null;
      state.travelsData = [];
    },
    REMOVE_TRAVEL_RECORD(state, payload) {
      state.travelsData = state.travelsData.filter(
        ({ docId }) => docId !== payload
      );
    },
    SET_CURRENT_TRAVEL(state, payload) {
      if (payload) {
        state.travelCurrent = state.travelsData.find(
          ({ id }) => id === payload
        );
      } else {
        state.travelCurrent = null;
      }
    },
    TOGGLE_BASE_MODAL(state, payload) {
      state.baseModal = payload;
    },
    TOGGLE_TRAVEL_FORM_MODAL(state) {
      state.travelFormModal = !state.travelFormModal;
    },
    TOGGLE_MOBILE_MENU(state) {
      state.mobileMenu = !state.mobileMenu;
    },
    UPDATE_USER(state, payload) {
      state.isAuthenticated = !!payload;
      state.me = payload;

      localStorage.setItem('isAuthenticated', state.isAuthenticated);
    }
  },
  actions: {
    async CREATE_TRAVEL({ dispatch }, data) {
      const db = firestore.collection('travels').doc();
      await db.set(data);
      await dispatch('GET_TRAVELS');
    },
    async GET_TRAVELS({ commit, state }) {
      const { me, travelsData } = state;
      const db = firestore.collection('travels');
      const response = await db.where('author', '==', me.uid).get();

      response.forEach(doc => {
        const isEntityFetched = travelsData.some(
          ({ docId }) => docId === doc.id
        );

        if (!isEntityFetched) {
          const data = {
            comment: doc.data().comment,
            destination: doc.data().destination,
            docId: doc.id,
            endDate: doc.data().endDate,
            id: doc.data().id,
            name: doc.data().name,
            startDate: doc.data().startDate
          };

          commit('ADD_TRAVEL_DATA', {
            ...data,
            ...getDateAttributes(data.startDate, data.endDate)
          });
        }
      });
    },
    async DESTROY_TRAVEL({ commit }, docId) {
      const db = firestore.collection('travels').doc(docId);
      await db.delete();
      commit('REMOVE_TRAVEL_RECORD', docId);
    },
    TERMINATE_SESSION({ commit }) {
      commit('CLEAR_TRAVELS_DATA');
      commit('UPDATE_USER', null);
    },
    TOGGLE_TRAVEL_FORM_MODAL({ commit, state }) {
      // improve scroll UX inside the modal
      const classMethod = state.travelFormModal ? 'remove' : 'add';
      window.scrollTo(0, 0);
      document.documentElement.classList[classMethod]('no-scroll');

      commit('TOGGLE_TRAVEL_FORM_MODAL');
    },
    async UPDATE_TRAVEL({ commit, dispatch }, { data, docId }) {
      const db = firestore.collection('travels').doc(docId);
      await db.update(data);
      commit('REMOVE_TRAVEL_RECORD', docId);

      await dispatch('GET_TRAVELS');
      commit('SET_CURRENT_TRAVEL', data.id);
    }
  },
  modules: {
  }
})
