<template>
  <div class="home container">
    <div class="home__content">
      <home-header
        :filteredTravelsCount="filteredTravelsData.length"
        :filterValue="filterValue"
        :setFilterValue="setFilterValue"
        :travelsCount="travelsData.length"
      />

      <travels-list
        :travelsData="filteredTravelsData"
      />

      <home-footer
        :printMonthPlan="printMonthPlan"
        :travelsCount="travelsData.length"
      />
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import HomeFooter from '@/components/home/HomeFooter'
import HomeHeader from '@/components/home/HomeHeader'
import TravelsList from '@/components/home/TravelsList'

export default {
  name: 'Home',
  components: {
    HomeFooter,
    HomeHeader,
    TravelsList
  },
  data () {
    return {
      filterByNextMonth: false,
      filterValue: undefined
    };
  },
  beforeMount () {
    window.addEventListener('afterprint', this.togglePrintingMode);
  },
  beforeUnmount () {
    window.removeEventListener('afterprint', this.togglePrintingMode);
  },
  computed: {
    ...mapState(['travelsData']),
    /**
     * Filter travels by selected value or by next month for print report
     * @returns {Array} - filterd travels or empty array literal
     */
    filteredTravelsData () {
      const { filterValue, filterByNextMonth, travelsData } = this;

      // filter travels by next month
      if (filterByNextMonth) {
        // get current month
        const currentMonth = new Date().getMonth();
        // get next month
        const nextMonthRaw = new Date().setMonth(currentMonth + 1);
        // get formated date
        const nextMonth = new Date(nextMonthRaw).toLocaleString('en-US', {
          month: '2-digit',
          year: 'numeric'
        });

        return travelsData.filter(travel => {
          // get start stravel date
          const travelStart = new Date(travel.startDate).toLocaleString('en-US', {
            month: '2-digit',
            year: 'numeric'
          });

          return travelStart === nextMonth;
        });
      }

      // filter travels by selected value
      if (filterValue) {
        return travelsData.filter(travel => travel.status === filterValue);
      }

      return travelsData;
    }
  },
  methods: {
    ...mapMutations(['TOGGLE_BASE_MODAL']),
    /**
     * Print button handler
     */
    printMonthPlan () {
      this.togglePrintingMode();

      if (this.filteredTravelsData.length) {
        // allow rendering to happen before print
        setTimeout(() => window.print(), 0);
      } else {
        this.togglePrintingMode();

        this.TOGGLE_BASE_MODAL({
          buttonResolve: 'Ok',
          text: 'Please add your travels first',
          title: 'There are nothing to print'
        });
      }
    },
    /**
     * Filter button handler
     */
    setFilterValue (value) {
      this.filterValue = value;
    },
    /**
     * Switch filter mode to print or default
     */
    togglePrintingMode() {
      this.filterByNextMonth = !this.filterByNextMonth;
    }
  }
}
</script>
