export default {
  methods: {
    /**
     * Returns inflected words between plural and singular forms
     *
     * @param {string} singular - word in singular form
     * @param {string} plural - word in plural form
     * @param {number} count - pluralization being based on the number of items
     * @returns {string}
     */
    inflect (singular, plural, count) {
      return count === 1 ? singular : plural;
    }
  }
}
