<template>
  <header class="header">
    <div class="header__side header__side--start">
      <h1 class="header__title">Travels</h1>
      <span class="header__subtitle">{{ subtitle }}</span>
    </div>

    <div class="header__side header__side--end">
      <div
        class="filter header__filter"
        ref="filter"
        :class="{ 'filter--is-open': isFilterMenu }"
      >
        <button
          v-show="travelsCount"
          class="filter__toggle"
          type="button"
          @click="filterMenuToggle"
        >
          <span class="filter__toggle-text">Filter by {{ filterBy }}</span>
          <img
            alt="Down"
            class="filter__toggle-icon"
            src="@/assets/icons/icon-caret-down.svg"
          />
        </button>

        <ul class="filter__menu" @click="handleFilterClick">
          <li
            v-for="(label, value) in filterMapping"
            class="filter__menu-item"
            :class="{ 'filter__menu-item--active': value === filterValue }"
            :key="value"
            :data-value="value"
          >
            {{ label }}
          </li>
          <li class="filter__menu-item filter__menu-item--reset">Reset</li>
        </ul>
      </div>

      <button
        class="add-button header__button"
        type="button"
        @click="newTravel"
      >
        <img
          alt="Add"
          class="add-button__icon"
          src="@/assets/icons/icon-plus.svg"
        />
        <span class="add-button__text">New Travel</span>
      </button>
    </div>
  </header>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'HomeHeader',
  props: {
    filteredTravelsCount: {
      required: true,
      type: Number
    },
    filterValue: {
      default: undefined,
      required: false,
      type: String
    },
    setFilterValue: {
      required: true,
      type: Function
    },
    travelsCount: {
      required: true,
      type: Number
    }
  },
  data () {
    return {
      filterMapping: {
        current: 'Current',
        next: 'Coming',
        past: 'Happened'
      },
      isFilterMenu: false
    }
  },
  computed: {
    filterBy () {
      const { filterMapping, filterValue } = this;
      return filterValue ? filterMapping[filterValue] : '';
    },
    subtitle () {
      const { filteredTravelsCount } = this;

      return filteredTravelsCount > 0
        ? `Total travel: ${filteredTravelsCount}`
        : 'So far there are no travels here';
    },
  },
  methods: {
    ...mapActions(['TOGGLE_TRAVEL_FORM_MODAL']),
    /**
     * Closes filter menu if user clicks outside of widget
     *
     * @param {HTMLElement} target Event target
     */
    filterMenuOutsideClick ({ target }) {
      if (!this.$refs.filter.contains(target)) {
        this.isFilterMenu = false;
      }
    },
    /**
     * Toggle filter menu
     */
    filterMenuToggle () {
      this.isFilterMenu = !this.isFilterMenu;
    },
    /**
     * Handle filter option click
     */
    handleFilterClick ({ target }) {
      const { value } = target.dataset;

      this.filterMenuToggle();
      this.setFilterValue(value);
    },
    /**
     * Open travel creation from
     */
    newTravel () {
      this.TOGGLE_TRAVEL_FORM_MODAL();
    },
  },
  watch: {
    isFilterMenu (state) {
      if (state) {
        window.addEventListener('click', this.filterMenuOutsideClick);
      } else {
        window.removeEventListener('click', this.filterMenuOutsideClick);
      }
    }
  }
}
</script>

