<template>
  <div class="travel-form-modal">
    <div class="travel-form-modal__backdrop" @click="handleDiscard"></div>

    <form
      class="travel-form"
      @submit.prevent="handleSubmit"
    >
      <h2 class="travel-form__title">
        <span v-if="!travelCurrent">New travel</span>
        <span v-else>Edit travel</span>
      </h2>

      <form-row>
        <form-control
          label="Travel name"
          name="name"
        >
          <input
            class="form-field"
            id="name"
            name="name"
            minlength="3"
            required
            type="text"
            v-model="form.name"
          />
        </form-control>

        <form-control
          label="Place to travel"
          name="destination"
        >
          <select
            class="form-field form-field--select"
            id="destination"
            name="destination"
            required
            v-model="form.destination"
          >
            <option disabled>Please select a country</option>
            <option
              v-for="(country, code) in countries"
              :key="code"
              :value="code"
            >
              {{ country }}
            </option>
          </select>
        </form-control>
      </form-row>

      <form-row>
        <form-control
          label="Starting from"
          name="startDate"
        >
          <input
            class="form-field form-field--date"
            id="startDate"
            name="startDate"
            required
            type="date"
            v-model="form.startDate"
          />
        </form-control>

        <form-control
          label="Ending at"
          name="endDate"
        >
          <input
            class="form-field form-field--date"
            id="endDate"
            name="endDate"
            required
            type="date"
            :disabled="!form.startDate"
            :min="form.startDate"
            v-model="form.endDate"
          />
        </form-control>
      </form-row>

      <form-row>
        <form-control
          label="Comment"
          name="comment"
        >
          <textarea
            class="form-field form-field--textarea"
            id="comment"
            name="comment"
            type="text"
            v-model="form.comment"
          />
        </form-control>
      </form-row>

      <div class="travel-form__actions">
        <button
          class="form-button form-button--cancel"
          type="button"
          @click="handleDiscard"
        >
          Cancel
        </button>

        <button
          class="form-button form-button--submit"
          type="submit"
        >
          <span v-if="!travelCurrent">Create</span>
          <span v-else>Update</span>
        </button>
      </div>

      <loader v-show="isLoading" />
    </form>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import { uid } from 'uid'
import MappingCountries from '@/components/mixins/MappingCountries'
import Loader from '@/components/Loader'
import { FormControl, FormRow } from '@/components/form'

export default {
  name: 'TravelForm',
  components: {
    FormControl,
    FormRow,
    Loader
  },
  mixins: [MappingCountries],
  data () {
    return {
      isLoading: false,
      form: {
        comment: null,
        destination: null,
        endDate: null,
        name: null,
        startDate: null
      }
    }
  },
  created () {
    const { travelCurrent } = this;

    if (travelCurrent) {
      this.form = { ...travelCurrent };
    }
  },
  computed: {
    ...mapState(['travelCurrent', 'me'])
  },
  methods: {
    ...mapActions(['CREATE_TRAVEL', 'TOGGLE_TRAVEL_FORM_MODAL', 'UPDATE_TRAVEL']),
    ...mapMutations(['TOGGLE_BASE_MODAL']),
    /**
     * Close travel creation from
     */
    closeForm() {
      this.TOGGLE_TRAVEL_FORM_MODAL();
    },
    /**
     * Render confirm modal before interrupting an interaction with the form
     */
    handleDiscard () {
      const { closeForm, form } = this;
      const isFormDirty = Object.values(form).some(val => !!val);

      if (isFormDirty) {
        this.TOGGLE_BASE_MODAL({
          buttonReject: 'Cancel',
          buttonResolve: 'Ok',
          onResolve: closeForm,
          text: 'Changes you made may not be saved',
          title: 'Leave form'
        });
      } else {
        closeForm();
      }
    },
    /**
     * Handle form submitting
     * Create of Update travel entity
     */
    async handleSubmit () {
      const { form, me, travelCurrent } = this;
      const userUid = me.uid;
      this.isLoading = true;

      if (!travelCurrent) {
        await this.CREATE_TRAVEL({
          ...form,
          author: userUid,
          id: uid(8),
        });
      } else {
        const { docId } = travelCurrent;
        await this.UPDATE_TRAVEL({
          data: {
            author: userUid,
            comment: form.comment,
            destination: form.destination,
            endDate: form.endDate,
            id: form.id,
            name: form.name,
            startDate: form.startDate
          },
          docId
        });
      }

      this.isLoading = false;
      this.closeForm();
    }
  },
  watch: {
    /**
     * Observes that the startDate date doesn't exceed endDate
     * If it's the case, resets endDate
     */
    ['form.startDate'] () {
      const { endDate, startDate } = this.form;

      if (endDate && startDate > endDate) {
        this.form.endDate = '';
      }
    }
  }
}
</script>

