const STATUS_CURRENT = 'current';
const STATUS_ERROR = 'error';
const STATUS_NEXT = 'next';
const STATUS_PAST = 'past';

/**
 * Returns the status of travel
 *
 * @param {number} startDiff - time difference of startDate and now
 * @param {number} endDiff - time difference of endDate and now
 * @returns {('Current'|'Error'|'Next'|'Past')}
 */
const getTravelStatus = (startDiff, endDiff) => {
  switch (true) {
    case startDiff < 0 && endDiff > 0:
      return STATUS_CURRENT;

    case startDiff < 0:
      return STATUS_PAST;

    case startDiff > 0:
      return STATUS_NEXT;

    default:
      return STATUS_ERROR;
  }
};

/**
 * Returns the days to begin of travel
 *
 * @param {number} diff - time difference of two dates
 * @returns {number}
 */
const getDaysLeft = diff => Math.round(diff / (1000 * 3600 * 24));

/**
 * @typedef {Object} DateAttr
 * @property {number} daysLeft - The days to begin of travel or zero
 * @property {('Current'|'Error'|'Next'|'Past')} status - The status of travel
 */

/**
 * Returns the additional date attributes to display
 *
 * @param {Date} startDate - travel start date
 * @param {Date} endDate - travel end date
 * @returns {DateAttr}
 */
const getDateAttributes = (startDate, endDate) => {
  const now = new Date();
  const startDiff = new Date(startDate) - now;
  const endDiff = new Date(endDate) - now.setHours(0,0,0,0);

  const status = getTravelStatus(startDiff, endDiff);
  const daysLeft = status === STATUS_NEXT ? getDaysLeft(startDiff) : 0;

  return {
    daysLeft,
    status
  };
};

export default getDateAttributes;
